//
// Form editors.scss
//

// Tinymce

.tox-tinymce {
  border: 1px solid $input-border-color !important;
}

.tox {
  .tox-menu{
    background-color: var(--#{$prefix}card-bg) !important;
    border-color: var(--#{$prefix}border-color) !important;
  }
  .tox-collection__group{
    border-color: var(--#{$prefix}border-color) !important;
  }
  .tox-collection__item{
    color: var(--#{$prefix}body-color) !important;
  }
  .tox-collection__item--active{
    background-color: var(--#{$prefix}border-color) !important;
  }
  .tox-collection__group-heading{
    background-color: var(--#{$prefix}border-color) !important;
  }
  .tox-collection__item--enabled{
    background-color: var(--#{$prefix}border-color) !important;
  }
  .tox-statusbar {
    border-top: 1px solid $light !important;
  }
  .tox-menubar,
  .tox-edit-area__iframe,
  .tox-statusbar {
    background-color: $card-bg !important;
    background: none !important;
  }
  .tox-mbtn {
    color: $gray-700 !important;
    &:hover:not(:disabled):not(.tox-mbtn--active) {
      background-color: $light !important;
    }
  }
  .tox-tbtn {
    &:hover {
      background-color: $light !important;
    }
  }
  .tox-tbtn--enabled{
    background-color: var(--#{$prefix}card-bg) !important;

    &:hover,
    &:active,
    &:focus{
      background-color: var(--#{$prefix}card-bg) !important;
    }
  }
  .tox-toolbar,
  .tox-toolbar__overflow,
  .tox-toolbar__primary {
    background: $light !important;
  }

  .tox-toolbar__primary {
    border-top-color: $light !important;
  }

  .tox-tbtn {
    color: $gray-700 !important;
    svg {
      fill: $gray-700 !important;
    }
  }

  .tox-edit-area__iframe {
    background-color: $card-bg !important;
  }

  .tox-statusbar a,
  .tox-statusbar__path-item,
  .tox-statusbar__wordcount {
    color: $gray-700 !important;
  }

  &:not([dir="rtl"]) .tox-toolbar__group:not(:last-of-type) {
    border-right: 1px solid darken($light, 5%) !important;
  }
}

// x editable

.editable-input {
  .form-control {
    display: inline-block;
  }
}

.editable-buttons {
  margin-left: 7px;
  .editable-cancel {
    margin-left: 7px;
  }
}
